:root {
  --main-font: "Roboto", sans-serif;
  --white-color: #fff;
  --orange-color: #ff6b08;
  --black-color: #000;
  --footer-backgraund-color: #f7f7f7;
  --red-color: #ff001b;
  --footer-text-color: #545454;
  --indent: 32px;
  --items: 1;
}

body {
  color: var(--black-color);
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 24px;
  transition: all .5s ease-in-out;
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
}

.no-scroll {
  overflow: hidden;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

ul, p {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4 {
  margin: 0;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media (max-width: 767.98px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
  }
}

.section {
  padding: 20px 0;
}

@media screen and (min-width: 768px) {
  .section {
    padding: 60px 0;
  }
}

.logo {
  color: var(--white-color);
  font-size: 30px;
  font-weight: 500;
  line-height: 1.16667;
}

.btn {
  cursor: pointer;
  color: var(--white-color);
  background-color: var(--orange-color);
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 13px 46px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  display: flex;
}

@media screen and (min-width: 480px) {
  .btn {
    padding: 14px 36px;
  }
}

.btn-goup {
  width: 60px;
  height: 60px;
  cursor: pointer;
  color: rgba(39, 46, 52, .5);
  background-color: #fff;
  border: 2px solid rgba(39, 46, 52, .5);
  border-radius: 50%;
  outline: none;
  font-size: 41px;
  transition: all .5s ease-in-out;
  position: fixed;
  bottom: 40px;
  right: 40px;
  transform: scale(1);
  box-shadow: 0 0 rgba(0, 0, 0, 0);
}

.btn-goup:hover {
  transform: scale(1.2);
  box-shadow: 0 0 18px #000;
}

.btn-goup__icon {
  fill: rgba(39, 46, 52, .5);
}

.is-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  margin: -1px;
  padding: 0;
  transition: all .5s ease-in-out;
  position: absolute;
  overflow: hidden;
}

.pagination {
  justify-content: center;
  display: flex;
}

.pagination-list {
  align-items: center;
  gap: 20px;
  display: flex;
}

.pagination-list__item-page {
  color: var(--orange-color);
  font-size: 14px;
}

.pagination-list__item-nav {
  background-color: var(--orange-color);
  color: var(--white-color);
  cursor: pointer;
  border-radius: 50px;
  padding: 5px 15px;
  font-size: 14px;
}

.backdrop {
  cursor: pointer;
  z-index: 1000;
  background-color: rgba(0, 0, 0, .2);
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}

